import React, { useState, useEffect } from "react";

const UNMOUNTED = "unmounted";
const EXITED = "exited";
const ENTERING = "entering";
const ENTERED = "entered";
const EXITING = "exiting";

const transitionStyles = {
  entering: { opacity: 0 },
  entered: { opacity: 1 },
  exiting: { opacity: 0 },
  exited: { opacity: 0 }
};


//Animation wrapper component. adds fade in animation on mount, on show change fades out.
const FadeInOut = ({ show, children, delay, duration, className, style }) => {
  const [status, setStatus] = useState(UNMOUNTED);

  useEffect(() => {
    if (show) {
      performEnter();
    } else if (status === ENTERING || status === ENTERED) {
      performExit();
    } else if (status === EXITED) {
      setStatus(UNMOUNTED);
    }
  }, [show]);

  const performEnter = () => {
      setStatus(ENTERING);
      setTimeout(() => {
        setStatus(ENTERED);
      }, (delay))
  };

  const performExit = () => {
    setStatus(EXITING);
    setTimeout(() => {
      setStatus(EXITED);
    }, duration);
  };

  if (status === UNMOUNTED) {
    return null;
  }

  return (
    <span
      className={className}
      style={{
        ...style,
        transition: `opacity ${duration}ms ease-in-out`,
        opacity: 0.1,
        ...transitionStyles[status]
      }}
    >
      {children}
    </span>
  );
};

FadeInOut.defaultProps = {
  show: false,
  delay: 0,
  duration: 300,
};

export default FadeInOut;
