import React, { useState, useEffect } from 'react';
import styles from '../styles/modules/RippleButton.module.css';

const RippleButton = ({ onClick, children, className, alt, celebrate, ripple }) => {
  const [ripples, setRipples] = useState([]);
  const [isExploding, setIsExploding] = useState(false);

  const ConfettiPiece = (index) => {

    return (
      <div key={index} className={`${styles.confettiPiece} ${isExploding ? styles[`explodeAnimation${index}`] : ''}`} />
    );
  };

  let confetti = [];
  for (let i = 0; i < 10; i++) {
    confetti.push(ConfettiPiece(i));
  }

  const handleAnimationEnd = (id) => {
    setRipples((prevRipples) => prevRipples.filter((ripple) => ripple.id !== id));
  };

  const handleClick = (e) => {
    e.preventDefault();
    onClick && onClick();
    if (ripple || ripple == undefined) {
      const rippleContainer = e.currentTarget.getBoundingClientRect();
      const x = e.clientX - rippleContainer.left - 10;
      const y = e.clientY - rippleContainer.top - 10;

      const newRipple = {
        x,
        y,
        id: Date.now(),
      };
      setRipples((prevRipples) => [...prevRipples, newRipple]);
    }
    if (celebrate) {
      setIsExploding(true);

      setTimeout(() => {
        setIsExploding(false);
      }, 3000);
    }
  };

  return (
    <div className={styles.container}>
      <div className={`${className ? className : ''} ${styles.rippleContainer}`} onClick={handleClick}>
        {ripples.map((ripple) => (
          <div
            key={ripple.id}
            className={`${styles.ripple} ${alt ? styles.altBackground : ''}`}
            style={{ left: `${ripple.x}px`, top: `${ripple.y}px` }}
            onAnimationEnd={() => handleAnimationEnd(ripple.id)}
          />
        ))}
        <div className={styles.children}>{children}</div>
      </div>
      {confetti}
    </div>
  );
};

export default RippleButton;
