___CSS_LOADER_EXPORT___.push([module.id, `/* Box sizing border-box for all elements */
html {
  box-sizing: border-box;
  background: var(--background-color);
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

::after,
::before{
  pointer-events: none;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove default padding */
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  padding: 0;
}

/* Set default font family and size */
body {
  font-family: 'Arial', sans-serif;
  font-size: 16px;
}

/* Remove list styles */
ul,
ol {
  list-style: none;
}

/* Remove default styles for links */
a {
  text-decoration: none;
  color: inherit;
}

button {
  border: none;
  cursor: pointer;
}

/* Remove default styles for form elements */
button,
input,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  margin: 0;
  outline: none;
}

select option:disabled {
  color: #808080;
}

input[type="radio"] {
  width: min-content;
}

input[type="radio"]:focus {
  outline: none;
}

/* Normalize line height */
body {
  line-height: 1.5;
}

/* Responsive images */
img {
  max-width: 100%;
  height: auto;
}`, "",{"version":3,"sources":["webpack://./src/styles/reset.css"],"names":[],"mappings":"AAAA,2CAA2C;AAC3C;EACE,sBAAsB;EACtB,mCAAmC;AACrC;;AAEA;;;EAGE,mBAAmB;AACrB;;AAEA;;EAEE,oBAAoB;AACtB;;AAEA,0BAA0B;AAC1B;;;;;;;;;;;;;;EAcE,SAAS;AACX;;AAEA,2BAA2B;AAC3B;;;;;;;;;;;;;EAaE,UAAU;AACZ;;AAEA,qCAAqC;AACrC;EACE,gCAAgC;EAChC,eAAe;AACjB;;AAEA,uBAAuB;AACvB;;EAEE,gBAAgB;AAClB;;AAEA,oCAAoC;AACpC;EACE,qBAAqB;EACrB,cAAc;AAChB;;AAEA;EACE,YAAY;EACZ,eAAe;AACjB;;AAEA,4CAA4C;AAC5C;;;;EAIE,oBAAoB;EACpB,kBAAkB;EAClB,SAAS;EACT,aAAa;AACf;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,aAAa;AACf;;AAEA,0BAA0B;AAC1B;EACE,gBAAgB;AAClB;;AAEA,sBAAsB;AACtB;EACE,eAAe;EACf,YAAY;AACd","sourcesContent":["/* Box sizing border-box for all elements */\nhtml {\n  box-sizing: border-box;\n  background: var(--background-color);\n}\n\n*,\n*:before,\n*:after {\n  box-sizing: inherit;\n}\n\n::after,\n::before{\n  pointer-events: none;\n}\n\n/* Remove default margin */\nbody,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np,\nul,\nol,\nfigure,\nblockquote,\ndl,\ndd {\n  margin: 0;\n}\n\n/* Remove default padding */\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np,\nul,\nol,\nfigure,\nblockquote,\ndl,\ndd {\n  padding: 0;\n}\n\n/* Set default font family and size */\nbody {\n  font-family: 'Arial', sans-serif;\n  font-size: 16px;\n}\n\n/* Remove list styles */\nul,\nol {\n  list-style: none;\n}\n\n/* Remove default styles for links */\na {\n  text-decoration: none;\n  color: inherit;\n}\n\nbutton {\n  border: none;\n  cursor: pointer;\n}\n\n/* Remove default styles for form elements */\nbutton,\ninput,\nselect,\ntextarea {\n  font-family: inherit;\n  font-size: inherit;\n  margin: 0;\n  outline: none;\n}\n\nselect option:disabled {\n  color: #808080;\n}\n\ninput[type=\"radio\"] {\n  width: min-content;\n}\n\ninput[type=\"radio\"]:focus {\n  outline: none;\n}\n\n/* Normalize line height */\nbody {\n  line-height: 1.5;\n}\n\n/* Responsive images */\nimg {\n  max-width: 100%;\n  height: auto;\n}"],"sourceRoot":""}]);
