import React, { useEffect, useState } from "react";
import { GiCoffeeMug } from "react-icons/gi";
import { Link, useLocation, useNavigate } from "react-router-dom";

import styles from "../styles/modules/Footer.module.css"

const Footer = () => {
    const navigate = useNavigate()
    const location = useLocation()
    const [isVisible, setIsVisible] = useState(true)

    const handleScroll = () => {
        const scrolledToBottom = window.scrollY + window.innerHeight >= document.documentElement.scrollHeight;
        setIsVisible(scrolledToBottom);
    };

    const [isThanking, setIsThanking] = useState(false)

    let timer1;
	const navigateTo = (destination, delay, openBlank) => {
		clearTimeout(timer1)
		timer1 = setTimeout(() => {
			if (openBlank) {
				window.open(destination, '_blank');
			} else {
				navigate(destination)
			}
		}, (delay || 10))
	}

    let timer2
    const thank = (e) => {
        e.preventDefault()
        setIsThanking(true)
        navigateTo('https://www.paypal.com/donate/?business=MVVJWUBMGV866&no_recurring=0&currency_code=GBP', 2000, true);
        clearTimeout(timer2)
        timer2 = setTimeout(() => {
            setIsThanking(false)
        }, 2500)
    }

    useEffect(() => {
        document.addEventListener("scroll", handleScroll);
        window.addEventListener("resize", handleScroll);
        return () => {
            document.removeEventListener("scroll", handleScroll);
            window.removeEventListener("resize", handleScroll);
        }
    }, []);

    useEffect(() => {
        handleScroll()
    }, [location])

    return (
        <footer className={isVisible ? "visible" : "invisible"}>
            <nav>
                <ul>
                    <li >
                        <div className={`${styles.thanks} ${isThanking ? "isVisible" : "invisible"}`}>
                            <span>
                                🥳
                                <span>T</span>
                                <span>h</span>
                                <span>a</span>
                                <span>n</span>
                                <span>k</span>
                                <span>s</span>
                                <span>!</span>
                                🎉
                            </span>
                        </div>
                        <Link onClick={thank}>
                            Buy me a coffee
                        </Link>
                    </li>
                    <li>
                        <Link to={"/pricing"}>
                            Pricing
                        </Link>
                    </li>
                    <li>
                        <Link to={"/legal"}>
                            Legal
                        </Link>
                    </li>
                    <li>
                        <Link to={"/policies"}>
                            Policies
                        </Link>
                    </li>
                </ul>
            </nav>
        </footer>
    )
}

export default Footer