import React, { useState, useEffect, useRef } from "react"
import styles from "../styles/modules/Liquid.module.css"
import getRandomNumber from "../utils/getRandomNumber.js"
import ToggleSwitch from "./ToggleSwitch.js"

import { FaDroplet, FaDropletSlash } from "react-icons/fa6";
import { PiCursorClickFill, PiCursorFill } from "react-icons/pi";

import RangeSlider from "./RangeSlider.js";
import FadeInOut from "./Animations/FadeInOut.js";

const LiquidParticle = ({ containerRef, blobTransitionClass, follows, offsetTop }) => {
    const cursorPos = useRef({ x: 0, y: 0 })
    const blobRef = useRef()
    const [maxSize, setMaxSize] = useState(0)
    const [variance, setVariance] = useState(100)
    const [blobSize, setBlobSize] = useState(0);
    const [blobPosition, setBlobPosition] = useState({
        top: 50, left: 50
    })
    const [blobBorderRadius, setBlobBoarderRadius] = useState({
        val1: 50, val2: 50, val3: 50, val4: 50, val5: 50, val6: 50, val7: 50, val8: 50,
    })

    let intervalId;
    useEffect(() => {
        intervalId = setInterval(() => {
            const size = getRandomNumber(10, maxSize)
            setBlobSize(size);
            setBlobPosition({
                top: getRandomNumber((variance * -1), variance) - ((size - 100) / 2),
                left: getRandomNumber((variance * -1), variance) - ((size - 100) / 2)
            })
            setBlobBoarderRadius({
                val1: getRandomNumber(30, 70),
                val2: getRandomNumber(30, 70),
                val3: getRandomNumber(30, 70),
                val4: getRandomNumber(30, 70),
                val5: getRandomNumber(30, 70),
                val6: getRandomNumber(30, 70),
                val7: getRandomNumber(30, 70),
                val8: getRandomNumber(30, 70),
            })
        }, getRandomNumber(1000, 1200));

        return () => clearInterval(intervalId);
    });

    useEffect(() => {
        const handleMove = (e) => {
            const bounds = 5;
    
            cursorPos.current = {
                x: (e.clientX || e.touches?.[0]?.clientX) + 50,
                y: (e.clientY || e.touches?.[0]?.clientY) + 50
            };

            const xMin = cursorPos.current.x <= bounds;
            const yMin = cursorPos.current.y <= bounds;
            const xMax = cursorPos.current.x >= window.innerWidth - bounds;
            const yMax = cursorPos.current.y >= window.innerHeight - bounds;
    
            if (xMin || yMin || xMax || yMax) {
                setMaxSize(0);
                setVariance(500);
            } else {
                if (follows) {
                    setMaxSize(80);
                    setVariance(50);
                } else {
                    setMaxSize(0);
                    setVariance(0);
                }
    
                blobRef.current.style.transform = `translateY(${cursorPos.current.y - 50 - (offsetTop || 0)}px) translateX(${cursorPos.current.x - 50}px)`;
            }
        };
    
        window.addEventListener("mousemove", handleMove);
        containerRef.current.addEventListener("touchstart", handleMove);
        containerRef.current.addEventListener("touchmove", handleMove);
    
        return () => {
            window.removeEventListener("mousemove", handleMove);
            containerRef.current.removeEventListener("touchstart", handleMove);
            containerRef.current.removeEventListener("touchmove", handleMove);
        };
    }, [follows, offsetTop]);
    

    let lastScrollY = 0
    useEffect(() => {
        const handleScroll = () => {
            const diff = window.scrollY - lastScrollY
            if (lastScrollY > window.scrollY) {
                blobRef.current.style.transform = `translateY(${cursorPos.current.y - diff}px) translateX(${cursorPos.current.x - 50}px)`
            } else if (lastScrollY < window.scrollY) {
                blobRef.current.style.transform = `translateY(${cursorPos.current.y - 150}px) translateX(${cursorPos.current.x - 50}px)`
            }
            lastScrollY = window.scrollY;
        };

        const handleScrollEnd = () => {
            blobRef.current.style.transform = `translateY(${cursorPos.current.y - 50}px) translateX(${cursorPos.current.x - 50}px)`
        }

        window.addEventListener('scroll', handleScroll);
        window.addEventListener('scrollend', handleScrollEnd);

        return () => {
            window.removeEventListener('scroll', handleScroll);
            window.removeEventListener('scrollend', handleScrollEnd);
        };
    }, []);

    useEffect(() => {
        //start in middle
        blobRef.current.style.transform = `translateY(${(window.innerHeight / 2) - 50}px) translateX(${(window.innerWidth / 2) - 50}px)`
    }, [])

    useEffect(() => {
        blobRef.current.style.animationDuration = `${getRandomNumber(32, 60)}s`
        blobRef.current.style.animationDelay = `${getRandomNumber(0, 22)}s`
    }, [])

    return (
        <div ref={blobRef} className={`${styles[blobTransitionClass]} ${styles.blob}`} style={{
            top: `${blobPosition.top}px`,
            left: `${blobPosition.left}px`,
            width: blobSize,
            height: blobSize,
            borderRadius: `${blobBorderRadius.val1}% ${blobBorderRadius.val2}% ${blobBorderRadius.val3}% ${blobBorderRadius.val4}% / ${blobBorderRadius.val5}% ${blobBorderRadius.val6}% ${blobBorderRadius.val7}% ${blobBorderRadius.val8}%`,
        }} />
    )
}

const BgLiquidParticle = ({ direction, bgOn, ...props }) => {
    const blobRef = useRef()
    const [blobSize, setBlobSize] = useState({ size: direction == "up" ? getRandomNumber(80, 110) : getRandomNumber(40, 60), variance: getRandomNumber(-10, 50) });
    const [blobPosition, setBlobPosition] = useState({
        top: 50, left: 50
    })
    const [blobBorderRadius, setBlobBoarderRadius] = useState({
        val1: 50, val2: 50, val3: 50, val4: 50, val5: 50, val6: 50, val7: 50, val8: 50,
    })

    let intervalId;
    useEffect(() => {
        intervalId = setInterval(() => {
            if (bgOn) {
                setBlobSize({ size: direction == "up" ? getRandomNumber(50, 100) : getRandomNumber(60, 90), variance: getRandomNumber(-10, 50) })
                setBlobPosition({
                    top: getRandomNumber(0, 100), left: getRandomNumber(0, 100)
                })
                setBlobBoarderRadius({
                    val1: getRandomNumber(25, 90),
                    val2: getRandomNumber(25, 90),
                    val3: getRandomNumber(25, 90),
                    val4: getRandomNumber(25, 90),
                    val5: getRandomNumber(25, 90),
                    val6: getRandomNumber(25, 90),
                    val7: getRandomNumber(25, 90),
                    val8: getRandomNumber(25, 90),
                })
            } else {
                setBlobSize({ size: 0, variance: 0 })
                setBlobPosition({
                    top: 0, left: 0
                })
            }
        },
            //Interval duration
            (direction == "up" ?
                getRandomNumber(5000, 6000) :
                getRandomNumber(1000, 2000)
            ));

        return () => clearInterval(intervalId);
    }, [bgOn]);

    useEffect(() => {
        if (blobRef.current) {
            blobRef.current.style.animationDuration = `${getRandomNumber(32, 60)}s`
            blobRef.current.style.animationDelay = `${getRandomNumber(0, 22)}s`
        }
    }, [blobRef.current])

    return (
        <FadeInOut show={bgOn} duration={5000}>
            <div
                ref={blobRef}
                className={`${styles.bgBlob} ${direction == "up" ? styles.bgBlobUp : styles.bgBlobDown}`}
                style={
                    {
                        transform: `translateY(${blobPosition.top}px), translateX(${blobPosition.left}px)`,
                        left: `${direction == "up" ? getRandomNumber(70, 90) : getRandomNumber(0, 15)}%`,
                        width: blobSize.size,
                        height: blobSize.size + blobSize.variance,
                        borderRadius: `${blobBorderRadius.val1}% ${blobBorderRadius.val2}% ${blobBorderRadius.val3}% ${blobBorderRadius.val4}% / ${blobBorderRadius.val5}% ${blobBorderRadius.val6}% ${blobBorderRadius.val7}% ${blobBorderRadius.val8}%`,
                    }}
                {...props}
            />
        </FadeInOut>
    )
}

export const LiquidControl = ({ follows, setFollows, bgOn, setBgOn, liquidAmount, setLiquidAmount }) => {
    return (
        <div className={styles.liquidControl}>
            <ToggleSwitch
                id={"followsCursor"}
                isChecked={follows}
                onChange={() => setFollows(prev => !prev)}
                checkedIcon={<PiCursorClickFill />}
                unCheckedIcon={<PiCursorFill />}
                aria-label={`Follows: ${follows ? "active" : "inactive"}`}
                label-position={"left"}
            />
            <ToggleSwitch
                id={"bgLiquid"}
                isChecked={bgOn}
                onChange={() => setBgOn(prev => !prev)}
                checkedIcon={<FaDroplet />}
                unCheckedIcon={<FaDropletSlash />}
                aria-label={`Background: ${bgOn ? "active" : "inactive"}`}
                label-position={"left"}
            />
            <RangeSlider value={liquidAmount} setValue={setLiquidAmount} min={0} max={10} />
        </div>
    )
}

const LiquidBackGround = ({ bgOn, liquidAmount }) => {
    const upParticles = []
    const downParticles = []

    for (let i = 0; i < liquidAmount; i++) {
        upParticles.push(<BgLiquidParticle key={i + "up"} bgOn={bgOn} direction="up" />)
        downParticles.push(<BgLiquidParticle key={i + "down"} bgOn={bgOn} direction="down" />)
    }

    return <div className={styles.blobContainer}>
        {upParticles}
        {downParticles}
    </div>
};

export const LiquidButton = ({ onClick, children }) => {
    const buttonRef = useRef()

    return (
        <button className={styles.liquidButton} onClick={onClick} ref={buttonRef}>
            <div className={styles.liquidContainer}>
                <LiquidParticle offsetTop={500} follows={true} blobTransitionClass={"blobTransition1"} />
                <LiquidParticle offsetTop={500} follows={true} blobTransitionClass={"blobTransition2"} />
                <LiquidParticle offsetTop={500} follows={true} blobTransitionClass={"blobTransition3"} />
                <LiquidParticle offsetTop={500} follows={true} blobTransitionClass={"blobTransition4"} />
                <LiquidParticle offsetTop={500} follows={true} blobTransitionClass={"blobTransition5"} />
                <LiquidParticle offsetTop={500} follows={true} blobTransitionClass={"blobTransition6"} />
                <LiquidParticle offsetTop={500} follows={true} blobTransitionClass={"blobTransition7"} />
                <LiquidParticle offsetTop={500} follows={true} blobTransitionClass={"blobTransition8"} />
                <LiquidParticle offsetTop={500} follows={true} blobTransitionClass={"blobTransition9"} />
                <LiquidParticle offsetTop={500} follows={true} blobTransitionClass={"blobTransition10"} />
            </div>
            {children}
        </button>
    )
}

const Liquid = ({ follows, bgOn }) => {
    const [liquidAmount, setLiquidAmount] = useState(6)
    const containerRef = useRef()

    let particles = [];

    for (let i = 0; i < 10; i++) {
        particles.push(<LiquidParticle containerRef={containerRef} follows={follows} blobTransitionClass={`blobTransition${i + 1}`} />)
    }

    return (<>
        <div ref={containerRef} className={styles.blobContainer}>
            <LiquidBackGround liquidAmount={liquidAmount} bgOn={bgOn} />
            {particles.map((particle) => particle)}
        </div>
    </>)
}

export default Liquid