import React, { useEffect } from "react";
import { Link, useLocation, useParams } from "react-router-dom";

import styles from "../styles/modules/LegalPage.module.css"

import webDesignAgreement from "../../public/assets/webDevelopmentServicesAgreement.txt"
import webMaintenanceAgreement from "../../public/assets/webMaintenanceAgreement.txt"
import graphicDesignServicesAgreement from "../../public/assets/graphicDesignServicesAgreement.txt"
import webHostingAgreement from "../../public/assets/WebHostingAgreement.txt"
import NotFoundPage from "./NotFoundPage";
import DownloadButton from "../components/DownloadButton";


const LegalPage = () => {
    const pages =
    {
        "web+development+services+agreement": webDesignAgreement,
        "web+maintenance+services+agreement": webMaintenanceAgreement,
        "graphic+design+services+agreement": graphicDesignServicesAgreement,
        "web+hosting+services+agreement": webHostingAgreement
    }
    const location = useLocation()

    const PDFViewer = ({ pdfUrl }) => {
        return (
            <iframe
                title="PDF Viewer"
                src={pdfUrl}
                width="100%"
                height="100%"
            />
        );
    };

    return (
        ((location.pathname == "/legal") || (location.pathname == "/legal/") || (`${location.pathname.slice(7)}` in pages)) ? (
            <div className={styles.container}>
                <section>
                    <h2>Legal</h2>
                    <p>Before utilizing our web development, maintenance, hosting, or graphic design services, we encourage you to review our legal agreements. These documents outline the terms and conditions that govern the relationship between [Your Company Name] and our clients. By accessing and using our services, you agree to comply with these terms. Please take the time to read the following agreements:</p>
                </section>
                <section>
                    <nav>
                        <ul>
                            <li className={location.pathname == "/legal/web+development+services+agreement" ? styles.activeTab : ""}>
                                <Link to={"/legal/web+development+services+agreement"}>
                                    Web development services agreement
                                </Link>
                            </li>
                            <li className={location.pathname == "/legal/web+maintenance+services+agreement" ? styles.activeTab : ""}>
                                <Link to={"/legal/web+maintenance+services+agreement"}>
                                    Web maintenance services agreement
                                </Link>
                            </li>
                            <li className={location.pathname == "/legal/web+hosting+services+agreement" ? styles.activeTab : ""}>
                                <Link to={"/legal/web+hosting+services+agreement"}>
                                    Web hosting services agreement
                                </Link>
                            </li>
                            <li className={location.pathname == "/legal/graphic+design+services+agreement" ? styles.activeTab : ""}>
                                <Link to={"/legal/graphic+design+services+agreement"}>
                                    Graphic design services agreement
                                </Link>
                            </li>
                        </ul>
                    </nav>
                </section>
                {location.pathname !== "/legal" && location.pathname !== "/legal/" && <section>
                    <p>It is important to understand and accept the terms outlined in each agreement. If you have any questions or concerns, feel free to contact us at [your contact email or phone number]. Your use of our services is contingent upon your acceptance and compliance with these agreements.</p>
                    <PDFViewer pdfUrl={pages[location.pathname.slice(7)]} />
                    <DownloadButton fileUrl={pages[location.pathname.slice(7)]} fileName={location.pathname.slice(7)}/>
                </section>}
            </div>
        ) : (
            <NotFoundPage />
        )
    )
}

export default LegalPage