import React from "react";
import { Link, useLocation } from "react-router-dom";

import styles from "../styles/modules/PoliciesPage.module.css"

import termsOfService from "../../public/assets/TermsOfService.txt"
import accessibilityPolicy from "../../public/assets/AccessibilityPolicy.txt"
import contactInformationPolicy from "../../public/assets/ContactInformationPolicy.txt"
import contentDisclaimerPolicy from "../../public/assets/ContentDisclaimerPolicy.txt"
import cookiePolicy from "../../public/assets/CookiePolicy.txt"
import CopyrightAndIntellectualPropertyPolicy from "../../public/assets/CopyrightAndIntellectualProperty.txt"
import privacyPolicy from "../../public/assets/privacyPolicy.txt"
import socialMediaAndExternalLinksPolicy from "../../public/assets/SocialMediaAndExternalLinksPolicy.txt"

import NotFoundPage from "./NotFoundPage";
import DownloadButton from "../components/DownloadButton";


const PoliciesPage = () => {
  const pages =
  {
    "terms+of+service": termsOfService,
    "accessibility": accessibilityPolicy,
    "contact+information": contactInformationPolicy,
    "content+disclaimer": contentDisclaimerPolicy,
    "cookies": cookiePolicy,
    "copyright+and+intellectual+property": CopyrightAndIntellectualPropertyPolicy,
    "privacy": privacyPolicy,
    "social+media+and+external+links": socialMediaAndExternalLinksPolicy
  }
  const location = useLocation()

  const PDFViewer = ({ pdfUrl }) => {
    return (
      <iframe
        title="PDF Viewer"
        src={pdfUrl}
        width="100%"
        height="100%"
      />
    );
  };

  return (
    ((location.pathname == "/policies") || (location.pathname == "/policies/") || (`${location.pathname.slice(10)}` in pages)) ? (
      <div className={styles.container}>
        <section>
          <h2>Policies</h2>
          <p>
            we value transparency and want to ensure that our users have a clear understanding of our policies. Please take a moment to review the following policies that govern your use of our website and services:
            By using our website and services, you agree to abide by these policies. If you have any questions or concerns, please reach out to us through our provided contact information.
          </p>
        </section>
        <section>
          <nav>
            <ul>
              <li className={location.pathname == "/policies/terms+of+service" ? styles.activeTab : ""}>
                <Link to={"/policies/terms+of+service"}>
                  <h3>Terms of Service:</h3>
                  <p> Outlines the terms and conditions governing the use of our website and services.</p>
                </Link>
              </li>
              <li className={location.pathname == "/policies/accessibility" ? styles.activeTab : ""}>
                <Link to={"/policies/accessibility"}>
                  <h3>Accessibility</h3>
                  <p>Details our commitment to providing an accessible and inclusive online experience for all users.</p>
                </Link>
              </li>
              <li className={location.pathname == "/policies/contact+information" ? styles.activeTab : ""}>
                <Link to={"/policies/contact+information"}>
                  <h3>Contact Information</h3>
                  <p>Find our contact details for any inquiries or concerns you may have.</p>
                </Link>
              </li>
              <li className={location.pathname == "/policies/content+disclaimer" ? styles.activeTab : ""}>
                <Link to={"/policies/content+disclaimer"}>
                  <h3>Content Disclaimer</h3>
                  <p>Describes the nature of the content on our website and the limitations of our liability.</p>
                </Link>
              </li>
              <li className={location.pathname == "/policies/cookies" ? styles.activeTab : ""}>
                <Link to={"/policies/cookies"}>
                  <h3>Cookies</h3>
                  <p>Explains how we use cookies to enhance your browsing experience and gather analytical data.</p>
                </Link>
              </li>
              <li className={location.pathname == "/policies/copyright+and+intellectual+property" ? styles.activeTab : ""}>
                <Link to={"/policies/copyright+and+intellectual+property"}>
                  <h3>Copyright and Intellectual Property</h3>
                  <p>Outlines our stance on copyright and intellectual property rights.</p>
                </Link>
              </li>
              <li className={location.pathname == "/policies/privacy" ? styles.activeTab : ""}>
                <Link to={"/policies/privacy"}>
                  <h3>Privacy</h3>
                  <p>Details how we collect, use, and protect your personal information.</p>
                </Link>
              </li>
              <li className={location.pathname == "/policies/social+media+and+external+links" ? styles.activeTab : ""}>
                <Link to={"/policies/social+media+and+external+links"}>
                  <h3>Social Media and External Links</h3>
                  <p>Guidelines for engaging with us on social media platforms.</p>
                </Link>
              </li>
            </ul>
          </nav>
        </section>
        {location.pathname !== "/policies" && location.pathname !== "/policies/" && <section>
          <PDFViewer pdfUrl={pages[location.pathname.slice(10)]} />
          <DownloadButton fileUrl={pages[location.pathname.slice(10)]} fileName={location.pathname.slice(10)}/>
        </section>}
      </div>
    ) : (
      <NotFoundPage />
    )
  )
}

export default PoliciesPage