  color: var(--primary-text-color);
}

html{
  overflow-y: scroll;
  overflow-x: hidden;
}

*{
  -webkit-tap-highlight-color: transparent
}

#root{

}

body{

}

main{
  padding-top: 10vw;
}

footer{
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: end;
  display: flex;
  font-size: x-small;
  z-index: 1;
  text-align: right;
  padding: 10px 10px 10px 0;
  transition: 0.3s opacity ease-in-out;
  opacity: 0;
  border-top: 1px solid var(--border-color);
}

footer nav,
footer nav ul{
  gap: 0;
}

footer a{
  color: var(--primary-text-color);
  transition: 0.1s all ease-in-out;
}

footer a:hover{
  color: var(--secondary-accent-color);
}

i {
  color: var(--secondary-text-color);
  font-style: normal;
}

a {
  color: white;
  transform-origin: left;
  position: relative;
  width: 100%;
  font-weight: 900;
  padding: 5px;
}

label{
  display: flex;
  justify-content: space-between;
  gap: 0.5rem;
  color: var(--primary-text-color);
}

/* Width and Track */
::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar:disabled{
  background: var(--primary-accent-color) !important;
}

::-webkit-scrollbar-track {
  background-color: var(--background-color);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background-color: var(--primary-accent-color);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background-color: var(--secondary-accent-color);
}

/* Corner */
::-webkit-scrollbar-corner {
  background-color: var(--primary-background-color);
}

/* Handle on hover */
main ::-webkit-scrollbar-thumb:hover {
  background-color: var(--secondary-accent-color);
}

/* Corner */
main ::-webkit-scrollbar-corner {
  background-color: var(--primary-background-color);
}

/* Basic styling for the tooltip */
[aria-label] {
  position: relative;
}

/* Styling for the tooltip content */
[aria-label]::before {
  content: attr(aria-label);
  /* Use the value of aria-label as the tooltip content */
  position: absolute;
  background-color: var(--primary-background-color);
  color: var(--primary-text-color);
  border-radius: 5px;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in-out 2s;
  font-size: small !important;
  font-weight: 600;
  padding: 0.1rem 0.5rem;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.8), 0px 5px 25px rgba(43, 43, 43, 0.5);
  z-index: 9999999999999 !important;
  pointer-events: none;
  white-space: nowrap;
}

[label-position="top"]::before {
  top: 0%;
  right: 50%;
  transform: translateX(50%);
}

[label-position="bottom"]::before {
  top: 250%;
  right: 50%;
  transform: translateX(50%);
}

[label-position="left"]::before {
  top: 50%;
  left: -10px;
  transform: translateY(-60%) translateX(-75%);
}

[label-position="right"]::before {
  top: 0%;
  left: 100%;
  transform: translateY(10%) translateX(10%);
}

[label-position="top-left"]::before {
  top: 0%;
  right: 100%;
  transform: translateY(-50%) translateX(0%);
}

[label-position="bottom-right"]::before {
  top: 100%;
  left: 100%;
  transform: translateY(-50%) translateX(-50%);
}

/* Show the tooltip on hover */
[aria-label]:hover::before {
  visibility: visible;
  opacity: 1;
}

h3 b {
  color: var(--secondary-accent-color) !important;
}

p b {
  color: var(--secondary-accent-color);
  filter: brightness(70%);
}

p i {
  color: var(--primary-text-color);
}

.stainedGlass {
  display: flex;
  height: min-content;
  width: 100% !important;
  position: relative;
  z-index: 1;
  overflow: hidden;
  background: var(--gradient1);
  mix-blend-mode: difference;
  filter: invert();
}

.stainedGlass::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  border-radius: 0;
  z-index: -1;
  box-shadow: inset 0 4px 8px rgba(0, 0, 0, 0.2) !important;
}

.stainedGlass h2{
  display: flex;
  align-items: center;
  justify-content: baseline;
  white-space: nowrap !important;
}

.stainedGlasss > {
  width: min-content;
}

.stainedGlass svg{
  display: flex;
  height: 100%;
  width: 30px;
}

.stainedGlass a svg{
  transition: all 0.2s ease-in-out;
}

.stainedGlass a:hover svg{
  transform: scale(125%);
}

.invisible{
  opacity: 0;
}

.visible{
  opacity: 1;
}

.fadeOut {
  opacity: 0;
  animation: fadeOut 0.5s forwards linear;
}

@keyframes fadeOut {
  from{
    opacity: 1;
    visibility: visible;
  }
  to{
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }
}


.fadeIn {
  opacity: 1;
  animation: fadeIn 0.25s forwards linear;
}

@keyframes fadeIn {
  from{
    opacity: 0;
    visibility: hidden;
  }
  to{
    opacity: 1;
    visibility: visible;
  }
}

.transitionUnset{
  transition: none !important;
}`, "",{"version":3,"sources":["webpack://./src/styles/global.css"],"names":[],"mappings":"AAAA;EACE,gCAAgC;AAClC;;AAEA;EACE,kBAAkB;EAClB,kBAAkB;AACpB;;AAEA;EACE;AACF;;AAEA;;AAEA;;AAEA;;AAEA;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,kBAAkB;EAClB,aAAa;EACb,8BAA8B;EAC9B,sBAAsB;EACtB,gBAAgB;EAChB,aAAa;EACb,kBAAkB;EAClB,UAAU;EACV,iBAAiB;EACjB,yBAAyB;EACzB,oCAAoC;EACpC,UAAU;EACV,yCAAyC;AAC3C;;AAEA;;EAEE,MAAM;AACR;;AAEA;EACE,gCAAgC;EAChC,gCAAgC;AAClC;;AAEA;EACE,oCAAoC;AACtC;;AAEA;EACE,kCAAkC;EAClC,kBAAkB;AACpB;;AAEA;EACE,YAAY;EACZ,sBAAsB;EACtB,kBAAkB;EAClB,WAAW;EACX,gBAAgB;EAChB,YAAY;AACd;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,WAAW;EACX,gCAAgC;AAClC;;AAEA,oBAAoB;AACpB;EACE,UAAU;AACZ;;AAEA;EACE,kDAAkD;AACpD;;AAEA;EACE,yCAAyC;AAC3C;;AAEA,WAAW;AACX;EACE,6CAA6C;AAC/C;;AAEA,oBAAoB;AACpB;EACE,+CAA+C;AACjD;;AAEA,WAAW;AACX;EACE,iDAAiD;AACnD;;AAEA,oBAAoB;AACpB;EACE,+CAA+C;AACjD;;AAEA,WAAW;AACX;EACE,iDAAiD;AACnD;;AAEA,kCAAkC;AAClC;EACE,kBAAkB;AACpB;;AAEA,oCAAoC;AACpC;EACE,yBAAyB;EACzB,uDAAuD;EACvD,kBAAkB;EAClB,iDAAiD;EACjD,gCAAgC;EAChC,kBAAkB;EAClB,kBAAkB;EAClB,UAAU;EACV,uCAAuC;EACvC,2BAA2B;EAC3B,gBAAgB;EAChB,sBAAsB;EACtB,8EAA8E;EAC9E,iCAAiC;EACjC,oBAAoB;EACpB,mBAAmB;AACrB;;AAEA;EACE,OAAO;EACP,UAAU;EACV,0BAA0B;AAC5B;;AAEA;EACE,SAAS;EACT,UAAU;EACV,0BAA0B;AAC5B;;AAEA;EACE,QAAQ;EACR,WAAW;EACX,4CAA4C;AAC9C;;AAEA;EACE,OAAO;EACP,UAAU;EACV,0CAA0C;AAC5C;;AAEA;EACE,OAAO;EACP,WAAW;EACX,0CAA0C;AAC5C;;AAEA;EACE,SAAS;EACT,UAAU;EACV,4CAA4C;AAC9C;;AAEA,8BAA8B;AAC9B;EACE,mBAAmB;EACnB,UAAU;AACZ;;AAEA;EACE,+CAA+C;AACjD;;AAEA;EACE,oCAAoC;EACpC,uBAAuB;AACzB;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,sBAAsB;EACtB,kBAAkB;EAClB,UAAU;EACV,gBAAgB;EAChB,4BAA4B;EAC5B,0BAA0B;EAC1B,gBAAgB;AAClB;;AAEA;EACE,WAAW;EACX,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,WAAW;EACX,YAAY;;EAEZ,gBAAgB;EAChB,WAAW;EACX,yDAAyD;AAC3D;;AAEA;EACE,aAAa;EACb,mBAAmB;EACnB,yBAAyB;EACzB,8BAA8B;AAChC;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,YAAY;EACZ,WAAW;AACb;;AAEA;EACE,gCAAgC;AAClC;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,UAAU;AACZ;;AAEA;EACE,UA