import React, { useRef } from "react";
import { useLocation } from "react-router-dom";

import FadeInOut from "../Animations/FadeInOut";

import styles from "../../styles/modules/ProjectsPage.module.css";

import { useOverlay } from "../..";

const TileView = ({ props }) => {
    const {
        view,
        setView,
        activeFilter,
        setActiveFilter,
        toggleSearchBar,
        setSubCategory,
        filteredData,
        setFilteredData,
    } = props

    const filteredRef = useRef()
    const location = useLocation();
    const { setOverlay, setExpandedHeader } = useOverlay()

    return (<>
        <div ref={filteredRef} className={`${styles.filteredContent} ${activeFilter ? styles.expanded : styles.collapsed} ${view == "gallery" ? styles.tileView : ""}`}
            onContextMenu={(e) => {
                e.preventDefault()
            }}>
            {filteredData && filteredData.length > 0 && filteredData.map((project, i) => {
                if (project.preview.type == "video" && (activeFilter == "all" || project.category == activeFilter)) {
                    return (
                        <FadeInOut show={true} delay={i*100} duration={500} key={project.name + i}>
                            <section
                                key={i}
                                id={"tile" + i}
                                className={`${i == filteredData.length / 2 ? styles.spotlight : ""} ${styles.card} `}
                                data-role={project.category}
                                data-role2={project.subCategory}
                                onClick={() => {
                                    setExpandedHeader(true)
                                    setOverlay(project.name)
                                }}>
                                <video
                                    controlsList="nodownload"
                                    loading="lazy"
                                    muted
                                    autoPlay={i == filteredData.length / 2 ? true : false}
                                    controls={i == filteredData.length / 2 ? true : false}
                                    playsInline
                                    loop
                                    id="myVideo">
                                    <source src={project.preview.url} type="video/mp4" />
                                    Your browser does not support the video tag.
                                </video>
                            </section>
                        </FadeInOut>
                    )
                }
                if (project.preview.type == "image" && (activeFilter == "all" || project.category == activeFilter)) {
                    return (
                        <FadeInOut show={true} delay={i*100} duration={500} key={project.name + i}>
                            <section
                                key={i}
                                id={"tile" + i}
                                className={`${styles.card} `}
                                data-role={project.category}
                                data-role2={project.subCategory}
                                onClick={() => {
                                    setExpandedHeader(true)
                                    setOverlay(project.name)
                                }}>
                                <img loading="lazy" src={project.preview.url} />
                            </section>
                        </FadeInOut>
                    )
                }
            })}
        </div>
    </>)
}

export default TileView