import React, { useState, useEffect } from "react";
import styles from "../styles/modules/ScrollDownIndicator.module.css"

const ScrollDownIndicator = ({top}) => {
    const [isVisible, setIsVisible] = useState(false);

    const handleScroll = () => {
        const scrollTop = document.documentElement.scrollTop;
        setIsVisible(scrollTop < 100);
    };

    useEffect(() => {
        setTimeout(() => {
            const scrollTop = document.documentElement.scrollTop;
            if(scrollTop < 100){
                setIsVisible(true)
            }else{
                setIsVisible(false)
            }
        }, 3000)
    }, [])

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    return (
        <div className={`${styles.scrollDownIndicator} ${isVisible ? styles.visible : ''}`} style={{top: top || ""}}>
            <div>
                 Go Down
            </div>
        </div>
    )
}

export default ScrollDownIndicator