tive;\n    font-size: 1.3rem;\n    z-index: 1;\n    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\n    height: 50px;\n}\n\n.invisible {\n    opacity: 0 !important;\n}\n\n.container .activeTab {\n    pointer-events: none;\n    margin-bottom: 0px;\n    position: relative;\n}\n\n.container .activeTab::after{\n    animation: expandIn 0.35s forwards linear;\n    content: \"\";\n    position: absolute;\n    bottom: 0%;\n    left: 0;\n    display: flex;\n    height: 5px;\n    width: 100%;\n    background: var(--primary-accent-color);\n    z-index: 1;\n}\n\n\n\n@keyframes expandIn {\n    from {\n        height: 0px;\n    }\n\n    to {\n        height: 5px;\n    }\n}\n\n.container .activeTab svg {\n    color: var(--primary-accent-color);\n}\n\n.rollingWordsContainer {\n    height: 4vw;\n    width: 100%;\n    display: flex;\n    flex-direction: column;\n    overflow: hidden;\n}\n\n.rollingWords {\n    display: flex;\n    flex-direction: column;\n    height: 100%;\n    width: 100%;\n    position: relative;\n    top: 75px;\n    opacity: 90%;\n}\n\n.rollingWords i {\n    display: inline-block;\n    position: absolute;\n    transform: rotateX(-90deg);\n    animation-timing-function: ease;\n    color: var(--primary-text-color) !important;\n    text-shadow: none;\n}\n\n.rollingWords i:nth-child(1) {\n    animation: rollDown 10s forwards infinite;\n    opacity: 0;\n}\n\n.rollingWords i:nth-child(2) {\n    animation: rollDown2 10s forwards infinite;\n    opacity: 0;\n}\n\n.rollingWords i:nth-child(3) {\n    animation: rollDown3 10s forwards infinite;\n    opacity: 0;\n}\n\n@keyframes rollDown {\n    0% {\n        top: -100px;\n        transform: rotateX(-90deg);\n    }\n\n    11% {\n        top: -74px;\n        transform: rotateX(0deg);\n        opacity: 1;\n    }\n\n    22% {\n        top: -74px;\n        transform: rotateX(0deg);\n        opacity: 1;\n    }\n\n    33% {\n        t