tems: center;\n    justify-content: center;\n}\n\n.container button svg {\n    padding: 2px;\n    transition: all 0.1s ease-in-out;\n    color: var(--primary-text-color);\n    height: 20px;\n    width: 20px;\n}\n\n.container button:hover svg {\n    color: var(--primary-background-color);\n}\n\n.container>button:active svg {\n    color: var(--primary-background-color);\n}\n\n.overlayContent img,\n.overlayContent svg,\n.overlayContent video {\n    object-fit: cover;\n    min-width: 300px;\n    width: 100vw;\n    height: 100%;\n    background: var(--primary-background-color);\n    transition: all 0.2s ease-in-out;\n    cursor: pointer;\n}\n\n.overlayContent {\n    margin: 10px;\n    margin-top: 50px;\n}\n\n.overlayContent>div>*:last-child {\n    margin-bottom: 20px;\n}\n\n.overlayContent>div {\n    display: flex;\n    flex-direction: column;\n    gap: 1rem;\n}\n\n.container {\n    position: fixed;\n    left: 0;\n    bottom: 0 !important;\n}\n\n.scroll {\n    position: fixed;\n    width: 40px;\n    height: 40px;\n    background: var(--primary-background-color);\n    border: 3px solid var(--border-color);\n    border-radius: 0.5rem;\n    position: absolute;\n    bottom: 27px;\n    left: 25px;\n    animation: down 3s infinite;\n    -webkit-animation: down 3s infinite;\n    cursor: pointer;\n    box-shadow: -5px 2px 10px rgba(0, 0, 0, 0.4);\n    transition: all 0.2s ease-in-out;\n}\n\n.scroll:hover {\n    animation: none;\n    background: var(--primary-accent-color);\n}\n\n.scroll:active {\n    border: 3px solid var(--primary-background-color);\n}\n\n.scroll::before {\n    content: '';\n    position: absolute;\n    top: 8px;\n    left: 11px;\n    width: 13px;\n    height: 13px;\n    border-left: 3px solid var(--primary-text-color);\n    border-bottom: 3px solid var(--primary-text-color);\n    transform: rotate(-45deg);\n}\n\n@keyframes down {\n\n    0%,\n    20%,\n    40% {\n        transform: translate(0);\n    }\n\n    10%,\n    30% {\n        transform: translateY(10px);\n    }\n}\n\n@-webkit-keyframes down {\n    0% {\n        transform: translate(0);\n    }\n\n    10% {\n        transform: translateY(15px);\n    }\n\n    20% {\n        transform: translate(0);\n    }\n}\n\n.controls {\n    position: fixed;\n    top: 5px;\n    right: 0px;\n    display: flex;\n    gap: 0px;\n    z-index: 1;\n}\n\n.expanded {\n    height: 100% !important;\n    max-height: 100vh !important;\n}\n\n/* Width and Track */\n.container ::-webkit-scrollbar {\n    background: transparent;\n    width: 6px;\n}\n\n.container ::-webkit-scrollbar:disabled {\n    background: transparent !important;\n}\n\n.container ::-webkit-scrollbar-track {\n    background: transparent !important;\n}\n\n/* Handle */\n.container ::-webkit-scrollbar-thumb {\n    background-color: var(--primary-accent-color);\n    border-radius: 0;\n    border: none;\n}\n\n/* Handle on hover */\n.container ::-webkit-scrollbar-thumb:hover {\n    background-color: var(--primary-accent-color);\n}\n\n/* Corner */\n.container ::-webkit-scrollbar-corner {\n    background-color: var(--primary-background-color);\n}"],"sourceRoot":""}]);
