import React, { useEffect, useState } from "react";
import { Link, Navigate, useLocation, useNavigate } from "react-router-dom";

import styles from "../../styles/modules/ProjectsPage.module.css"

import { projectDataArray } from "../../../data/projectData";

import TileView from "./TileView";
import GalleryView from "./GalleryView";
import Search from "../SearchBar"
import { FaFilter, FaSearch } from "react-icons/fa";
import { HiRectangleStack } from "react-icons/hi2";
import { MdViewQuilt } from "react-icons/md";

const ProjectViewer = () => {
    const location = useLocation();
    const navigate = useNavigate()

    const [view, setView] = useState("gallery")
    const [activeFilter, setActiveFilter] = useState();
    const [toggleSearchBar, setToggleSearchBar] = useState(false)
    const [filterActive, setFilterActive] = useState(false)
    const [subCategory, setSubCategory] = useState()
    const [filteredData, setFilteredData] = useState(projectDataArray)
    const [show, setShow] = useState(false)

    useEffect(() => {
        const pathSegments = location.pathname.slice(1).split('/');
        setActiveFilter(pathSegments[0]);

        const subCategory = pathSegments.length > 1 ? pathSegments[1] : undefined;
        setSubCategory(subCategory);

        if (subCategory) {
            const filteredData = projectDataArray.filter(project => project.subCategory == subCategory)
            setFilteredData(filteredData)
        } else {
            setFilteredData(projectDataArray)
        }
    }, [location.pathname]);

    const props = {
        show:show,
        setshow:setShow,
        view: view,
        setView: setView,
        activeFilter: activeFilter,
        setActiveFilter: setActiveFilter,
        toggleSearchBar: toggleSearchBar,
        setToggleSearchBar: setToggleSearchBar,
        filterActive: filterActive,
        setFilterActive: setFilterActive,
        subCategory: subCategory,
        setSubCategory: setSubCategory,
        filteredData: filteredData,
        setFilteredData: setFilteredData,
    }

    const handleMenuView = () => {
        const views = [
            "tile",
            "gallery"
        ]
        const currentIndex = views.findIndex(val => val == view)
        setView(prev => {
            if (currentIndex == views.length - 1) {
                return views[0]
            } else {
                return views[currentIndex + 1]
            }
        })
    }

    useEffect(()=>{
        setShow(true)
    },[])

    return (<>
        <div className={styles.container}>
            <div className={styles.controls}>
                <div className={styles.filteredContentControls}>
                    <button aria-label={view.slice(0, 1).toUpperCase() + view.slice(1)} label-position="left" className={`${styles.filterButton} ${styles.filterButton2}`}
                        onClick={handleMenuView}>
                        {view == "tile" && <MdViewQuilt />}
                        {view == "gallery" && <HiRectangleStack />}
                    </button>
                    <button className={`${toggleSearchBar ? styles.activeTab : ""}`} onClick={(e) => setToggleSearchBar(prev => !prev)}>
                        <FaSearch />
                    </button>
                    <button className={`${filterActive ? styles.activeTab : ""}`} onClick={(e) => setFilterActive(prev => !prev)}>
                        <FaFilter />
                    </button>
                </div>
                <span>
                    <Search toggleSearchBar={toggleSearchBar} setToggleSearchBar={setToggleSearchBar} />
                    {filterActive && <nav>
                        <ul>
                            <li>
                                <Link
                                    className={`${activeFilter == "all" ? styles.activeTab : styles.inactiveTab}`}
                                    to="/all"  >
                                    all
                                </Link>
                            </li>
                            <li className={`${activeFilter == "live" ? styles.activeTab : styles.inactiveTab}`}>
                                <Link
                                    className={`${activeFilter == "live" ? styles.activeTab : styles.inactiveTab}`}
                                    to="/live" >
                                    live
                                </Link>
                                {activeFilter == "live" && <Link
                                    className={`${(subCategory == "webDev") ? styles.activeTab : styles.inactiveTab}`}
                                    to="/live/webDev" >
                                    webDevelopment
                                </Link>}
                            </li>
                            <li className={`${activeFilter == "sideProjects" ? styles.activeTab : styles.inactiveTab}`}>
                                <Link
                                    className={`${activeFilter == "sideProjects" ? styles.activeTab : styles.inactiveTab}`}
                                    to="/sideProjects" >
                                    projects
                                </Link>
                                {activeFilter == "sideProjects" && (
                                    <Link
                                        className={`${subCategory == "webDev" ? styles.activeTab : styles.inactiveTab}`}
                                        to="/sideProjects/webDev" >
                                        webDevelopment
                                    </Link>
                                )}
                            </li>
                            <li
                                className={`${(activeFilter == "design" || activeFilter == "design/logos" || activeFilter == "design/typography") ? styles.activeTab : styles.inactiveTab}`}>
                                <Link
                                    className={`${activeFilter == "design" ? styles.activeTab : styles.inactiveTab}`}
                                    to="/design" >
                                    design
                                </Link>
                                {activeFilter == "design" && <Link className={`${subCategory == "logos" ? styles.activeTab : styles.inactiveTab}`} to="/design/logos" >logos</Link>}
                                {activeFilter == "design" && <Link className={`${subCategory == "typography" ? styles.activeTab : styles.inactiveTab}`} to="/design/typography" >typography</Link>}
                                {activeFilter == "design" && <Link className={`${subCategory == "icons" ? styles.activeTab : styles.inactiveTab}`} to="/design/icons" >icons</Link>}
                            </li>
                        </ul>
                        <p>showing <b>{filteredData.length}</b>/{projectDataArray.length} projects</p>
                    </nav>}
                </span>
            </div>
            {view == "tile" && <TileView props={props} />}
            {view == "list" && <ListView props={props} />}
            {view == "gallery" && <GalleryView props={props} />}
        </div>
        {view == "gallery" && <div className={styles.miniMap}>
            {filteredData && filteredData.length > 0 && filteredData.map((project, i) => {
                return (<div
                    label-position={"top"}
                    onClick={() => navigate(`#${project.name}`)}
                    key={i}
                    aria-label={project.name}
                    id={i + "miniMapBox"}
                    className={styles.miniMapBox}
                    style={{
                        borderTopColor: project.subCategory == "webDev" ? "var(--primary-accent-color)" : project.category == "design" ? "var(--secondary-accent-color)" : "var(--color-mix2)",
                        left: `${i * 10}%`
                    }} />)
            })}
            <div>
                <p style={{
                    borderLeft: "5px solid var(--primary-accent-color)"
                }}>Web Development</p>
                <p style={{
                    borderLeft: "5px solid var(--secondary-accent-color)"
                }}>Design</p>
            </div>
        </div>}
    </>)
}

export default ProjectViewer