import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { MdClose } from "react-icons/md";
import { acceptCookies, getCookie } from "../utils/cookies"

import styles from "../styles/modules/CookiesConsent.module.css"
import RippleButton from "./RippleButton";

import cookieSVG from "../../public/assets/cookie.svg"

const CookiesConsent = ({setHasPrompted, isVisible, setIsVisible}) => {

    useEffect(() => {
        const consentCookie = getCookie("cookieConsent")
        if (consentCookie) {
            setIsVisible(false)
            setHasPrompted(true)
        } else {
            setHasPrompted(false)
            setIsVisible(true)
        }
    })

    return (
        <div className={`${styles.container} ${isVisible ? styles.expand : ""}`}>
            <button onClick={() => setIsVisible(false)}>
                <MdClose />
            </button>
            <img src={cookieSVG}/>
            <form>
                <span>
                    <p>
                        This website uses cookies to ensure you get the best experience on our website.
                        <br />
                        By using our site, you acknowledge that you have read and understand our <Link to="/policies">Policies</Link>.
                    </p>
                </span>
                <span>
                    <Link onClick={(e)=>{
                        e.preventDefault()
                        setHasPrompted(true)
                        setIsVisible(false)
                        acceptCookies()
                    }}>
                        Accept
                    </Link>
                    <Link onClick={(e)=>{
                        e.preventDefault()
                        setIsVisible(false)
                        setHasPrompted(true)
                    }}>
                        Reject
                    </Link>
                </span>
            </form>
        </div>
    )
}

export default CookiesConsent