import React, { useEffect, useRef, useState } from "react";
import styles from "../styles/modules/ContactPage.module.css"
import RippleButton from "../components/RippleButton";
import { GiCheckMark } from "react-icons/gi";
import { MdOutlineLocationOn } from "react-icons/md";
import { FaWhatsapp } from "react-icons/fa6";
import { IoMdPaperPlane } from "react-icons/io";
import ToggleSwitch from "../components/ToggleSwitch"
import { MdClose } from "react-icons/md";
import FadeInOut from "../components/Animations/FadeInOut";

const ContactPage = () => {
    const subjectOptions = [
        "General", "Web services", "Report a bug"
    ]

    const formRef = useRef()
    const textareaRef = useRef()
    const [message, setMessage] = useState("")
    const [receiveUpdates, setReceiveUpdates] = useState(true)
    const [senderEmail, setSenderEmail] = useState("")
    const [subject, setSubject] = useState(subjectOptions[0])
    const [error, setError] = useState("")

    const handleTextareaChange = (e) => {
        setMessage(e.target.value)
    }

    const handleSendClick = async (e) => {
        const data = {
            from: senderEmail,
            subject: subject,
            text: message,
            receiveUpdates: receiveUpdates
        };

        try {
            if (message && senderEmail && subject) {
                const response = await fetch('http://localhost:3001/send-email', {
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify(data),
                });

                const responseData = await response.json();
                if (response.ok) {
                    setError()
                } else {
                    setError(responseData.error);
                }
            }
        } catch (error) {
            console.error('Error:', error);
            setError(error.toString());
        }
    };

    const handleClear = (e) => {
        setReceiveUpdates(false)
        setSenderEmail("")
        setMessage("")
        setSubject(subjectOptions[0])
    }

    return (
        <FadeInOut show={true} duration={500}>
            <div className={styles.container}>
                <section>
                    <ul>
                        <li>
                            <p>
                                <IoMdPaperPlane />
                                noodlebenji2960@gmail.com
                            </p>
                        </li>
                        <li>
                            <p>
                                <FaWhatsapp />
                                +34 680 84 08 91
                            </p>
                        </li>
                        <li>
                            <p>
                                <MdOutlineLocationOn />
                                La Rioja, Spain
                            </p>
                        </li>
                    </ul>
                </section>
                <section>
                    <form ref={formRef}>
                        <div>
                            <span>
                                <textarea
                                    required
                                    ref={textareaRef}
                                    className={styles.textarea}
                                    placeholder="Type your message here..."
                                    value={message}
                                    onChange={handleTextareaChange}
                                />
                                <iframe
                                    src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d1511529.5455960482!2d-3.7227782355072145!3d42.274122741091965!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0xd454b924ef7dcbb%3A0x787ffa5ea69b298e!2sLa%20Rioja!5e0!3m2!1sen!2ses!4v1708619856418!5m2!1sen!2ses"
                                    allowfullscreen=""
                                    loading="lazy"
                                    referrerpolicy="no-referrer-when-downgrade" />
                            </span>
                            <label>
                                Your email:
                                <input style={{ outline: error == "Invalid email" ? "1px solid red" : "1px solid transparent" }} required type="text" value={senderEmail} onChange={(e) => setSenderEmail(e.target.value)} placeholder="john.smith@email.com" />
                            </label>
                            <label>
                                Subject:
                                <select style={{ outline: error == "Invalid subject" ? "1px solid red" : "1px solid transparent" }} required value={subject} onChange={(e) => setSubject(e.target.value)}>
                                    {subjectOptions.map((option, i) => <option key={i} value={option}>
                                        {option}
                                    </option>)}
                                </select>
                            </label>
                            <label style={{ outline: error == "Invalid option" ? "1px solid red" : "1px solid transparent" }}>
                                I would like to receive updates:
                                <ToggleSwitch required={true} id={"updatesInput"} isChecked={receiveUpdates} onChange={(e) => setReceiveUpdates(prev => !prev)} checkedIcon={<GiCheckMark />} unCheckedIcon={<MdClose />} />
                            </label>
                        </div>
                        <p className={styles.description}>
                            Thank you for visiting my web design portfolio. If you are interested in collaborating on a project or have any inquiries regarding my services, I would love to hear from you.
                            <br></br>
                            <br></br>
                            Feel free to reach out using the contact form, and I will respond promptly. Your vision is important, and I am dedicated to bringing it to life through thoughtful and innovative design. I look forward to creating something great together.
                        </p>
                        <span>
                            <p>{error}</p>
                            <RippleButton onClick={handleClear} alt={true}>
                                Clear
                            </RippleButton>
                            <RippleButton onClick={handleSendClick}>
                                Send
                            </RippleButton>
                        </span>
                    </form>
                </section>
            </div>
        </FadeInOut>
    )
}

export default ContactPage