import React, { useEffect, useRef, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";

import { projectDataArray } from "../../../data/projectData";

import styles from "../../styles/modules/ProjectsPage.module.css";

import FadeInOut from "../Animations/FadeInOut";

const GalleryView = ({ props }) => {
    const {
        activeFilter,
        setActiveFilter,
        setSubCategory,
        filteredData,
        setFilteredData,
        show,
    } = props
    const filteredRef = useRef()
    const location = useLocation();
    const navigate = useNavigate()

    useEffect(() => {
        const pathSegments = location.pathname.slice(1).split('/');
        setActiveFilter(pathSegments[0]);

        const subCategory = pathSegments.length > 1 ? pathSegments[1] : undefined;
        setSubCategory(subCategory);

        if (subCategory) {
            const filteredData = projectDataArray.filter(project => project.subCategory == subCategory)
            setFilteredData(filteredData)
        } else {
            setFilteredData(projectDataArray)
        }
    }, [location.pathname]);


    const handleScroll = () => {
        const sections = document.querySelectorAll(`section`);
        sections.forEach((section, i) => {
            if (filteredRef.current.scrollTop >= (section.offsetTop - (window.innerHeight * 0.5))) {
                document.getElementById(i + "miniMapBox").style.borderBottomColor = "var(--border-color)"
                section.style.opacity = "0.95"
                section.style.pointerEvents = "all"
            } else {
                document.getElementById(i + "miniMapBox").style.borderBottomColor = "transparent"

                section.style.opacity = "0"
                section.style.pointerEvents = "none"
            }
            if (filteredRef.current.scrollTop >= (section.offsetTop - (window.innerHeight * 0.1))) {
                document.getElementById(i + "miniMapBox").style.borderBottomColor = "transparent"
            }
        });
    };

    useEffect(() => {
            const { hash } = location;
            if (hash) {
                const targetElement = document.querySelector("#"+hash.slice(1));
                if (targetElement) {
                    targetElement.scrollIntoView();
                }
            }
    }, [location.hash]);

    return (<>
        <div ref={filteredRef} id={"scrollTarget"} onScroll={handleScroll} className={`${styles.filteredContent} ${activeFilter ? styles.expanded : styles.collapsed} ${styles.galleryView}`}
            onContextMenu={(e) => {
                e.preventDefault()
            }}>
            {filteredData && filteredData.length > 0 && filteredData.map((project, i) => {
                if (project.preview.type == "video" && (activeFilter == "all" || project.category == activeFilter)) {
                    return (
                        <FadeInOut show={show} delay={i*500} duration={500} key={project.name + i}>
                            <section
                                key={i}
                                id={project.name}
                                className={`${styles.galleryCard}`}
                                data-role={project.category}
                                data-role2={project.subCategory}>
                                <video
                                    onClick={() => navigate(`/projects/${project.name}`)}
                                    autoPlay={true}
                                    loop={true}
                                    controlsList="nodownload"
                                    loading="lazy"
                                    muted
                                    controls={false}
                                    playsInline
                                    id="myVideo">
                                    <source src={project.preview.url} type="video/mp4" />
                                    Your browser does not support the video tag.
                                </video>
                                <span>
                                    <h2>{project.name}</h2>
                                    <b>{project.category}</b>
                                    <i>{project.subCategory}</i>
                                    <p>{project.description}</p>
                                </span>
                            </section>
                        </FadeInOut>
                    )
                }
                if (project.preview.type == "image" && (activeFilter == "all" || project.category == activeFilter)) {
                    return (
                        <FadeInOut show={show} delay={i*500} duration={500} key={project.name + i}>
                            <section
                                key={i}
                                id={project.name}
                                className={`${styles.galleryCard} `}
                                data-role={project.category}
                                data-role2={project.subCategory}>
                                <img
                                    loading="lazy"
                                    src={project.preview.url}
                                    onClick={() => navigate(`/projects/${project.name}`)} />
                                <span>
                                    <h2>{project.name}</h2>
                                    <b>{project.category}</b>
                                    <i>{project.subCategory}</i>
                                    <p>{project.description}</p>
                                </span>
                            </section>
                        </FadeInOut>
                    )
                }
            })}
        </div>
    </>)
}

export default GalleryView